<template>
    <div id="edit-customer">
        <h3>Edit Customer</h3>
        <div class="row">
            <form @submit.prevent="updateCustomer" class="col s12">
                <div class="row">
                    <div class="input-field col s12">
                        <input disabled type="text" v-model="customer_id" required>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="name" required>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="designation" required>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="contact" required>
                    </div>
                </div>

                <button type="submit" class="btn blue">Add Customer</button>
                <router-link to="/" class="btn grey">Cancel</router-link>
            </form>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'edit-customer',
    data () {
        return {
            customer_id: null,
            name: null,
            designation: null,
            contact: null            
        }
    },
    beforeRouteEnter (to, from, next) {
        db.collection('customers').where('customer_id', '==', to.params.customer_id).get().then(querySnapshot => {querySnapshot.forEach(doc => {
            next(vm => {
                vm.customer_id = doc.data().customer_id
                vm.name = doc.data().name
                vm.designation = doc.data().designation
                vm.contact = doc.data().contact
            })
        })})
    },
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData () {
            db.collection('customers').where('customer_id', "==", this.$route.params.customer_id).get().then(querySnapshot => {querySnapshot.forEach(doc => {
                this.customer_id = doc.data().customer_id
                this.name = doc.data().name
                this.designation = doc.data().designation
                this.contact = doc.data().contact
            })})
        },
        updateCustomer () {
            db.collection('customers').where('customer_id', "==", this.$route.params.customer_id).get().then(querySnapshot => {querySnapshot.forEach(doc => {
                doc.ref.update({
                    customer_id: this.customer_id,
                    name: this.name,
                    designation: this.designation,
                    contact: this.contact
                })
                .then(() => {
                    this.$router.push({name: 'view-customer', params: {customer_id: this.customer_id}})
                })
            })})
        }
    }
}
</script>




