<template>
    <div id="view-customer">
        <ul class="collection with-header">
            <li class="collection-header"><h4>{{name}}</h4></li>
            <li class="collection-item">Customer/Lead ID: {{customer_id}}</li>
            <li class="collection-item">Designation/Occupation: {{designation}}</li>
            <li class="collection-item">Contact Details: {{contact}}</li>
        </ul>
        <router-link to="/" class="btn grey">Back</router-link>
        <button @click="deleteCustomer" class="btn red">Delete</button>

        <div class="fixed-action-btn">
            <router-link v-bind:to="{name: 'edit-customer', params: {customer_id: customer_id}}" class="btn-floating btn-large red">
                <i class="fa fa-pencil"></i>
            </router-link>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'view-customer',
    data () {
        return {
            customer_id: null,
            name: null,
            designation: null,
            contact: null            
        }
    },
    beforeRouteEnter (to, from, next) {
        db.collection('customers').where('customer_id', '==', to.params.customer_id).get().then(querySnapshot => {querySnapshot.forEach(doc => {
            next(vm => {
                vm.customer_id = doc.data().customer_id
                vm.name = doc.data().name
                vm.designation = doc.data().designation
                vm.contact = doc.data().contact
            })
        })})
    },
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData () {
            db.collection('customers').where('customer_id', "==", this.$route.params.customer_id).get().then(querySnapshot => {querySnapshot.forEach(doc => {
                this.customer_id = doc.data().customer_id
                this.name = doc.data().name
                this.designation = doc.data().designation
                this.contact = doc.data().contact
            })})
        },
        deleteCustomer () {
            if(confirm('Are you sure that you want to delete this entity, sire?')) {
                db.collection('customers').where('customer_id', "==", this.$route.params.customer_id).get().then(querySnapshot => {querySnapshot.forEach(doc => {
                    doc.ref.delete()
                    this.$router.push('/')
                })})
            }
        }
    }
}
</script>




