<template>
    <div id="new-customer">
        <h3>New Customer</h3>
        <div class="row">
            <form @submit.prevent="saveCustomer" class="col s12">
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="customer_id" required>
                        <label>Customer/Lead ID</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="name" required>
                        <label>Customer/Lead Name</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="designation" required>
                        <label>Designation/Occupation</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="contact" required>
                        <label>Contact Details</label>
                    </div>
                </div>

                <button type="submit" class="btn blue">Add Customer</button>
                <router-link to="/" class="btn grey">Cancel</router-link>
            </form>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'new-customer',
    data () {
        return {
            customer_id: null,
            name: null,
            designation: null,
            contact: null                        
        }
    },
    methods: {
        saveCustomer () {
            db.collection('customers').add({
                customer_id: this.customer_id,
                name: this.name,
                designation: this.designation,
                contact: this.contact
            })
            .then(docRef => this.$router.push('/'))
            .catch(error => console.log(err))
        }
    }
}
</script>




