<template>
    <div id="dashboard">
        <ul class="collection with-header">
            <li class="collection-header"><h4>Customers (Leads)</h4></li>
            <li v-for="customer in customers" v-bind:key="customer.id" class="collection-item">
                <div class="chip">{{customer.designation}}</div>{{customer.customer_id}}:{{customer.name}}

                <router-link class="secondary-content" v-bind:to="{name: 'view-customer', params: {customer_id: customer.customer_id}}">
                    <i class="fa fa-eye"></i>
                </router-link>
            </li>
        </ul>

        <div class="fixed-action-btn">
            <router-link to="/new" class="btn-floating btn-large blue">
                <i class="fa fa-plus"></i>
            </router-link>
        </div>
    </div>
</template>

<script>

import db from './firebaseInit'
export default {
    name: 'dashboard',
    data () {
        return {
           customers: [] 
        }
    },
    created () {
        db.collection('customers').orderBy('customer_id').get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                const data = {
                    'id': doc.id,
                    'customer_id': doc.data().customer_id,
                    'name': doc.data().name,
                    'designation': doc.data().designation,
                    'contact': doc.data().contact

                }
                this.customers.push(data)
            })
        })
    }
}
</script>




